
*{
  margin: 0;
  padding: 0;
}

a{
  text-decoration: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}


#menu {
    flex-shrink: 0;
    width: 120px;
    height: 100%;
    padding: 30px 10px;
    border-right: 1px dashed rgba(0,0,0,0.3);
}
.menu-item{
    width: 100%;
    display: block;
    text-align: center;
    font-size: 16px;
    height: 24px;
    margin-bottom: 14px;
}

.active-menu{
    color: burlywood;
    background-color: aquamarine;
}
.about {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap; 
    justify-content: center;
}
.info{
    text-align: center;
}
h2{
    width: 100%;
    font-size: 32px;
    margin-bottom: 20px;
}

p{
    width: 100%;
    font-size: 18px;
}
.example-emot{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#d3-learn{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
}

#page-content{
    flex-grow: 1;
    height: 100vh;
}
